let that = this;
export default {
	name: 'UniPopup',
	props: {
		// 开启动画
		animation: {
			type: Boolean,
			default: true
		},
		// 弹出层类型，可选值，top: 顶部弹出层；bottom：底部弹出层；center：全屏弹出层
		type: {
			type: String,
			default: 'center'
		},
		// 是否开启自定义
		custom: {
			type: Boolean,
			default: false
		},
		// maskClick
		maskClick: {
			type: Boolean,
			default: true
		},
		show:{
			type:Boolean,
			
		}
	},
	data() {
		return {
			ani: '',
			show: false,
			mask: 0
		};
	},
	created() {
		uni.setStorageSync('maskNo', 0);
	},
	onLoad() {
		that=this;
	},
	methods: {
		clear() {},
		open() {
			this.show = true;
			this.$nextTick(() => {
				setTimeout(() => {
					this.ani = 'uni-' + this.type;
				}, 30);
			});
			// let mask = uni.getStorageSync('maskNo');
			// mask = mask + 1;
			// uni.setStorageSync('maskNo', mask);
			// uni.hideTabBar({});
		},
		close(kk) {
			if (!this.maskClick && type) return;

			// let mask = uni.getStorageSync('maskNo');
			// mask = mask - 1;
			// uni.setStorageSync('maskNo', mask);
			// console.log(mask);
			// if (mask <= 0) {
			// 	uni.showTabBar({});
			// } else {
			// 	uni.hideTabBar({});
			// }
			this.ani = '';
			this.$nextTick(() => {
				setTimeout(() => {
					this.show = false;
				}, 300);
			});
		}
	}
};
